<template>
    <!-- 咨询记录-->
    <div class="recordDetail">
        <h6 class="title">
            咨询记录[由教师填写]
            <div class="btn_edit" @click="isEdit=true" v-show="!isEdit">编辑</div>
        </h6>
        <div class="xueyeBox">
            <div class="child">
                学业规划：第
                <el-select v-model="num"  placeholder="1" :disabled="!isEdit">
                    <el-option v-for="(item) in 20" :key="index" :lable="item" :value="item">
                    </el-option>
                </el-select>
                次
            </div>
            <div class="child">
                日期：
                <el-date-picker
                    v-model="date"  
                    :disabled="!isEdit"
                    type="date"
                    placeholder="请选择访谈日期"
                    value-format="yyyy-MM-dd">
                </el-date-picker>
            </div>
            <div class="theme">
	        	主题训练：
	        	<el-select v-model="themeList" multiple placeholder="请选择训练主题" :multiple-limit='3'>
				    <el-option
				      v-for="item in themeOptions"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				</el-select>
	        </div>
        </div>
        <div class="contentBox">
            <p class="title">学习过程中的感悟和体会</p>
            <div class="detail">
                <p v-if="!isEdit" v-html="reviewRecord.replace(/\n/g,'</br>')"></p>
                <textarea v-else  placeholder="学生的成长经历，最多输入500个字!"  maxlength="500"  v-model="reviewRecord" onfocus="this.placeholder=''"></textarea>
            </div>
        </div>
        <div class="contentBox">
            <p class="title">学生反馈</p>
            <div class="detail">
                <p v-if="!isEdit" v-html="thisContentrecord.replace(/\n/g,'</br>')"></p>
                <textarea v-else placeholder="学生描述的学习情况，最多输入500个字!" maxlength="500" v-model="thisContentrecord" onfocus="this.placeholder=''"></textarea>
            </div>
        </div>
        <div class="contentBox">
            <p class="title">教师评语</p>
            <div class="detail">
                <p v-if="!isEdit" v-html="improve.replace(/\n/g,'</br>')"></p>
                <textarea v-else placeholder="最多输入500个字!" maxlength="500" v-model="improve"  onfocus="this.placeholder=''"></textarea>
            </div>
        </div>
        <div class="btn_save" @click="storeConsultRecords()" v-show="isEdit" :class="isSave ? 'btn_save' : 'btn_unsave'">保存</div>
    </div>
</template>
<script>
export default {
    // components: { Yuanxiaoku },
    // props: [ 'message' ],
    name: 'recordDetail',
    data() {
        return {
            isEdit:true,
            // 学业规划次数列表
            guihuaList: '',
            // 添加日期
            date: '',
            theme: '',//主题
            num: '',//学业规划次·数
            planId: '',
            // 复盘记录
            reviewRecord: '',
            // 本次辅导内容记录
            thisContentrecord: '',
            // 规划总结与改进
            improve: '',


            isSave: false,

			themeList: [],
            themeOptions: [
            	{ value: '时间管理', label: '时间管理' }, 
            	{ value: '目标管理', label: '目标管理' }, 
            	{ value: '归因训练', label: '归因训练' }, 
            	{ value: '自信心提升', label: '自信心提升' }, 
            	{ value: '自我激励', label: '自我激励' }, 
            	{ value: '思维导图', label: '思维导图' }, 
            	{ value: '自我提问', label: '自我提问' }, 
            	{ value: '情绪管理', label: '情绪管理' }, 
            	{ value: '人际管理', label: '人际管理' }, 
            	{ value: '注意力训练', label: '注意力训练' }
            ]

        }
    },
    methods: {
        // 保存成功提示
        successMessage() {
            this.$message({
                type: 'success',
                message: '保存成功'
            });
        },
        // 保存有问题提示
        unSuccess(detail) {
            this.$message({
                type: 'info',
                message: detail
            });
        },
        // 获取学业规划次数结果
        getNumList() {
            var token = window.sessionStorage.getItem('ymtxToken');
            var _this = this;
            this.$ajax.post(this.G_uri + '/academicPlanCategory/getNumList', {
                },{
                    headers:{
                        token: token
                    }
                })
                // 监听数据返回
                .then(function(res) {
                    _this.guihuaList = res.data.data;
                })
                .catch(function(error) {
                    console.log(error)
                })
        },
        // 读取初步访谈记录
        getDetailConsultRecords(){
            let _this=this;
            let planId = this.planId;
            this.$ajax.post(this.G_uri + '/assessQuestion/detailConsultRecords',{
            	planId: planId
            },{
                headers:{
                    token:window.sessionStorage.getItem('ymtxToken')
                }
            }).then(res=>{
                let data = res.data.data;
                if(res.data.code==2000){
                    if(data.date) {
                        _this.date = data.date
                        _this.num = data.num;
                        _this.theme = data.theme;
                        _this.reviewRecord = data.reviewRecord;
                        _this.thisContentrecord = data.thisContentrecord;
                        _this.improve = data.improve;
                        _this.isEdit = false;
                    }else {
                        _this.isEdit = true;
                    }
                }
            })
        },
        // 保存初步访谈记录
        storeConsultRecords() {
            let _this=this;
            let date = this.date;
            let num = this.num;
            let theme = this.theme;
            let reviewRecord = this.reviewRecord;
            let thisContentrecord = this.thisContentrecord;
            let improve = this.improve;

            if((date&&theme&&reviewRecord&&thisContentrecord&&improve) == false) {
                return;
            }
            this.$ajax.post(this.G_uri + '/plan/storeConsultRecords',{
                date: date,
                num: num,
                reviewRecord: reviewRecord,
                thisContentrecord: thisContentrecord,
                improve: improve
            },{
                headers:{
                    token:window.sessionStorage.getItem('ymtxToken')
                }
            }).then(res=>{
                if(res.data.code==2000){
                    _this.isEdit = false;
                    _this.successMessage();
                }else {

                }
            })
        },  
        isAllowSave() {
            let date = this.date;
            let num = this.num;
            let theme = this.theme;
            let reviewRecord = this.reviewRecord;
            let thisContentrecord = this.thisContentrecord;
            let improve = this.improve
            if((date&&theme&&reviewRecord&&thisContentrecord&&improve) == false) {
                this.isSave = false;
            }else {
                this.isSave = true;
            }
        }
    },
    created() {
    	if(this.$router.id) {
    		this.planId = this.$router.id;
    		this.isEdit = false;
    	}else {
    		this.isEdit = true;
    	}
        // this.getNumList();
        // this.storeConsultRecords();
    },
    watch: {
        date() {
            this.isAllowSave();
        },
        theme() {
            this.isAllowSave();
        },
        reviewRecord() {
            this.isAllowSave();
        },
        thisContentrecord() {
            this.isAllowSave();
        },
        improve() {
        	this.isAllowSave();
        },
        themeList() {
        	console.log(this.themeList)
        }
    }
}
</script>
<style type="text/css" lang='less' scoped>
.recordDetail {
    margin-left: 20px;
    background-color: #fff;
    padding: 10px 20px;
    h6.title {
        line-height: 60px;
        font-size: 17px;
        color: #348DF1;
        font-weight: bolder;
        letter-spacing: 2px;
        padding-left: 30px;
        background: url(/imgs/careerArchives/icon_biaoti.png) no-repeat 0px center;
        .btn_edit {
            float: right;
            width: 62px;
            height: 26px;
            border-radius: 3px;
            text-align: center;
            line-height: 26px;
            font-size: 14px;
            background: #3a90f3;
            position: relative;
            font-weight: normal;
            top: 17px;
            color: #fff;
            cursor: pointer;
        }
    }
    .xueyeBox {
        padding-bottom: 26px;
        border-bottom: 1px solid #F0EFEF;
        margin-bottom: 18px;
        height: 42px;
        font-weight: 600;
        .child {
            float: left;
            margin-right: 30px;
        }
    }
    .theme {
    	font-weight: 600;
    	line-height: 34px;
    	margin-bottom: 20px;
    	input {
    		display: inline-block;
    		font-weight: normal;
    		border: 1px solid #DCDFE6 !important;
			height: 34px;
			line-height: 34px;
			padding: 0 10px;
			width: 450px;
    	}
    }
    .contentBox {
        padding-bottom: 18px;
        .title {
            line-height: 40px;
            padding-left: 14px;
            color: #3D90F6;
            font-size: 15px;
            font-weight: 600;
            position: relative;
            &:before {
                content: '';
                width: 7px;
                height: 7px;
                border-radius: 2px;
                background-color: #3D90F6;
                line-height: 40px;
                position: absolute;
                left: 0;
                top: 16px;
            }
        }
        textarea {
            padding: 5px 10px;
            font-size: 14px;
            width: 100%;
            box-sizing: border-box;
            border: 1px solid #CDCDCD;
            min-height: 174px;
            line-height: 20px;
            display: block;
        }
        textarea:disabled {
            background-color: #F5F7FA;
            cursor: no-drop;
        }
        .detail {
            p {
                padding: 5px 10px;
                font-size: 14px;
                width: 100%;
                box-sizing: border-box;
                border: 1px solid #CDCDCD;
                min-height: 174px;
                line-height: 20px;
                background-color: #F5F7FA;
                cursor: no-drop;
            }
        }
    }
    .btn_save {
	    width: 96px;
	    height: 34px;
	    text-align: center;
	    line-height: 34px;
	    border-radius: 3px;
	    font-size: 16px;
	    color: #fff;
	    margin: 10px auto 20px;
	    background: #3b91f4;
	    cursor: pointer;
    }
    .btn_unsave {
        .btn_save;
        background-color: #dfdfdf;
        cursor: no-drop;
    }
}
</style>
<style type="text/css" lang='less'>
.recordDetail {

	.child {
		.el-select {
			width: 60px;
		}
		.el-date-editor {
			width: 200px;
		}	
	}
	.theme{
		.el-select {
			width: 350px;
			.el-tag {
				margin-right: 5px;
			} 
			.el-select__tags-text {
				color: #3492f9;
			}
		}
	}
	.el-input__inner  {
        border: 1px solid #DCDFE6 !important;
        border-radius: 4px;
    }
}
</style>
